<template>
  <v-dialog v-model="dialog" max-width="600">
    <v-card>
      <v-card-title>
        <span class="headline">Register Yourself</span>
      </v-card-title>
      <v-card-text>
        <v-container grid-list-md>
          <v-layout wrap>
            <v-flex xs6>
              <v-text-field 
                label="Legal first name*"
                hint="Your cooperation is mandatory."
                persistent-hint
                required
              ></v-text-field>
            </v-flex>
            <v-flex xs6>
              <v-text-field
                label="Legal last name*"
                hint="Your cooperation is mandatory."
                persistent-hint
                required
              ></v-text-field>
            </v-flex>
            <v-flex xs12>
              <v-text-field 
                label="Email*"
                hint="Your cooperation is mandatory."
                persistent-hint
                required
              ></v-text-field>
            </v-flex>
            <v-flex xs12>
              <v-text-field 
                label="Password*" 
                type="password"
                hint="Your cooperation is mandatory."
                persistent-hint 
                required
              ></v-text-field>
            </v-flex>
            <v-flex xs12>
              <v-text-field 
                label="Retype Password*" 
                type="password"
                hint="Your cooperation is mandatory."
                persistent-hint 
                required
              ></v-text-field>
            </v-flex>
          </v-layout>
        </v-container>
        <small>*indicates required field</small>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="indigo darken-1" flat @click="dialog = false">Close</v-btn>
        <v-btn color="indigo darken-1" flat @click="dialog = false">Register</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>

import { EventBus, openRegisterDialog } from '@/scripts/event-bus'

export default {
  data() {
    return {
      dialog: false,
    }
  },
  mounted() {
    EventBus.$on(openRegisterDialog, () => this.dialog = true)
  }
}

</script>
