<template>
  <div class="company-card">
    <v-card>
        <v-img
          class="square-image"
          :src="company.image"
          aspect-ratio="1.0"
          contain
        ></v-img>

      <v-card-title primary-title>
        <div>
          <h3 class="headline mb-0">{{ company.name }}</h3>
          <div> {{ company.description }} </div>
        </div>
      </v-card-title>

      <v-card-title>
        <div>
          <h3 class="headline mb-0"><b>CEO:</b>{{ ' ' + company.ceo }}</h3>
        </div>
      </v-card-title>


      <v-card-actions>
        <h2 class="headline mb-0"> {{ '$' + company.market_value + ' million' }} </h2>
        <v-btn v-if="!user.cart[comp] && isShopping" icon flat large color="indigo" @click="addToCart">
          <v-icon>add_shopping_cart</v-icon>
        </v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>

import { User } from '@/scripts/user'

export default {
  props: {
    company: Object,
    isShopping: Boolean
  },
  data() {
    return {
      comp: JSON.stringify(this.company),
      user: User
    }
  },
  methods: {
    addToCart() {
      if (!User.cart[this.comp]) {
        User.cart[this.comp] = true
        this.user.cart[this.comp] = true
      }

      this.$forceUpdate()
    },
    removeFromCart() {
      if (User.cart[this.comp]) {
        User.cart[this.comp] = false
        this.user.cart[this.comp] = false
      }

      this.$forceUpdate()
    }
  },
  mounted() {

  }
}


</script>

<style scoped>

.company-card {
  padding: 0em 10em 0em 10em
}

.square-image {
  max-width: 100px;
}

</style>