<template>
  <div>
    <div v-if="admin">
      <v-toolbar dark fixed color="indigo">
        <v-btn icon dark @click="triggerOpenAdd">
          <v-icon>add</v-icon>
        </v-btn>
        <v-toolbar-title>Average: ${{average}} million</v-toolbar-title>
        <span class="small-divider"></span>
        <v-toolbar-title>Range: ${{range}} million</v-toolbar-title>
        <span class="small-divider"></span>
        <v-toolbar-title>Total: ${{total}} million</v-toolbar-title>
      </v-toolbar>
      <v-layout align-center column class="off-the-top indigo lighten-4">
        <div v-for="(item, i) in items" :key="item.name" style="padding-bottom: 2em">
          <Company :company="item" :isShopping="false"/>
          <v-layout row justify-center>
            <v-btn dark color="indigo darken-2" @click="triggerOpenEdit(item)">Edit</v-btn>
            <v-btn dark color="deep-orange" @click="triggerOpenDelete(item, i)">Delete</v-btn>
          </v-layout>
        </div>
      </v-layout>
    </div>
    <div v-else>
      <div style="font-size: 5em">
        You're not supposed to be here.
      </div>
    </div>
  </div>
</template>

<script>

import axios from 'axios'
import Company from '@/components/Company'
import { User } from '@/scripts/user'
import { EventBus, openDeleteDialog, openEditDialog, companyWasDeleted, companyWasEdited, companyWasAdded } from '@/scripts/event-bus'

export default {
  components: {
    Company
  },
  data() {
    return {
      admin: false,
      items: [],
      average: 0,
      range: 0,
      total: 0,
      selectedIndex: 0
    }
  },
  methods: {
    setItems() {
      axios({
        method: 'get',
        url: 'http://lest-cst336.herokuapp.com/final/api/products.php',
      })
      .then(response1 => {
        this.items = response1.data
        this.setEverything()
      });
    },
    setEverything() {
      this.setItems()

      this.total = 0

      for (let item of this.items) {
        this.total += Number(item.market_value)
      }

      this.total = this.total.toFixed(2)

      this.average = (this.total / this.items.length).toFixed(2)

      const values = this.items.map(item => Number(item.market_value))

      this.range = (Math.max(...values) - Math.min(...values)).toFixed(2)
    },
    triggerOpenDelete(company, index) {
      this.selectedIndex = index
      User.company = company
      EventBus.$emit(openDeleteDialog)
    },
    triggerOpenEdit(company) {
      User.company = company
      EventBus.$emit(openEditDialog)
    },
    triggerOpenAdd() {
      EventBus.$emit(openEditDialog)
    }
  },
  mounted() {
    EventBus.$on(companyWasDeleted, () => {
      this.items.splice(this.selectedIndex, 1)
    })

    EventBus.$on(companyWasEdited, () => {
      this.setEverything()
    })

    EventBus.$on(companyWasAdded, () => {
      this.setEverything()
    })

    this.admin = User.isAdmin

    if (this.admin) {
      this.setItems()
    }
  }
}

</script>

<style scoped>

.off-the-top {
  padding-top: 5em;
}

.small-divider {
  padding: 0em 2em 0em 2em;
}

</style>
