<template>
  <v-layout justify-center align-center fill-height column>
    <div class="company-title">
      {{message}}
    </div>
    <div v-if="showSearch">
      <v-progress-circular
        v-if="categories.length == 0"
        indeterminate
        color="indigo"
      ></v-progress-circular>
      <v-layout v-else align-center column>
        <v-text-field
          class="input-width"
          color="indigo darken-1"
          label="Search for a top-parent company (e.g. Alphabet)"
          v-model="searchTerm"
        ></v-text-field>
        <v-layout row>
          <v-checkbox
            class="checkbox-offset"
            color="indigo"
            v-for="(category, index) in categories"
            :key="index"
            :label="category.catName"
            :value="index + 1"
            v-model="selected"
          ></v-checkbox>
        </v-layout>
        <h1>Market value (in $million)</h1>
        <v-layout row>
          <v-text-field
            color="indigo"
            v-model="price[0]"
            class="mt-0 range-input"
            hide-details
            single-line
            type="number"
          ></v-text-field>
          <v-range-slider
            color="indigo"
            :max="1000000"
            :min="0"
            :step="25000"
            v-model="price"
          ></v-range-slider>
          <v-text-field
            color="indigo"
            v-model="price[1]"
            class="mt-0 range-input"
            hide-details
            single-line
            type="number"
          ></v-text-field>
        </v-layout>
        <v-btn flat icon color="indigo" @click="getProducts">
          <v-icon>search</v-icon>
        </v-btn>
      </v-layout>
    </div>
  </v-layout>
</template>

<script>
import axios from 'axios'

export default {
  props: {
    message: String,
    showSearch: Boolean
  },
  data() {
    return {
      categories: [],
      selected: [],
      searchTerm: '',
      price: [100000, 900000]
    }
  },
  methods: {
    getProducts() {
      if (this.searchTerm != '') {
        axios({
          method: 'get',
          url: 'http://lest-cst336.herokuapp.com/final/api/products.php',
          params: {
            search: this.searchTerm
          }
        })
        .then(response1 => {
          if (response1.data != '') {
            this.$router.push({name: 'shop', params: {items: response1.data}})
          }
        });
      }
      else if (this.selected.length > 0) {
        axios({
          method: 'get',
          url: 'http://lest-cst336.herokuapp.com/final/api/products.php',
          params: {
            category: this.selected.join(','),
            from: this.price[0],
            to: this.price[1]
          }
        })
        .then(response1 => {
          this.$router.push({name: 'shop', params: {items: response1.data}})
        });
      }
    }
  },
  mounted() {
    if (this.showSearch) {
      axios({
        method: 'get',
        url: 'http://lest-cst336.herokuapp.com/final/api/categories.php',
      })
      .then(response1 => {
        this.categories = response1.data

        for (let i = 0; i < this.categories.length; i++) {
          this.selected.push(i + 1)
        }
      });
    }
    
  }
}

</script>


<style scoped>

.company-title {
  font-size: 8em;
  text-align: center;
}

.input-width {
  width: 70%;
}

.checkbox-offset {
  padding: 0em 1em 0em 1em;
}

.range-input {
  width: 8em;
  padding: 0em 1em 0em 1em;
}

</style>
