<template>
  <v-speed-dial top right direction="bottom" class="always-somewhere">
    <template v-slot:activator>
      <v-btn color="indigo darken-3" dark fab @click="checkAdmin">
        <v-icon large>explore</v-icon>
      </v-btn>
    </template>
    <div class="offset-button">
      <v-btn round dark color="indigo darken-2" class="offset-button" @click="goHome">
        Home
        <v-icon right>home</v-icon>
      </v-btn>
    </div>
    <div class="offset-button">
      <v-btn v-if="admin" round dark color="indigo" class="offset-button" @click="goAdmin">
        Admin
        <v-icon right>remove_red_eye</v-icon>
      </v-btn>
      <v-btn v-else round dark color="indigo" class="offset-button" @click="triggerOpenCheckout">
        Checkout
        <v-icon right>shopping_cart</v-icon>
      </v-btn>
    </div>
    <div class="offset-button">
      <v-btn v-if="admin" round dark color="indigo lighten-1" class="offset-button" @click="logout">
        Logout
        <v-icon right>arrow_back</v-icon>
      </v-btn>
      <v-btn v-else round dark color="indigo lighten-1" class="offset-button" @click="triggerOpenLogin">
        Login
        <v-icon right>account_circle</v-icon>
      </v-btn>
    </div>
    <!-- <div class="offset-button">
      <v-btn v-if="!admin" round dark color="indigo lighten-2" @click="triggerOpenRegister">
        Register
        <v-icon right>face</v-icon>
      </v-btn>
    </div> -->
  </v-speed-dial>
</template>

<script>

import { EventBus, openLoginDialog, openRegisterDialog, openCheckoutDialog } from '@/scripts/event-bus'
import { User } from '@/scripts/user'
import { setTimeout } from 'timers';

export default {
  data() {
    return {
      admin: false
    }
  },
  methods: {
    triggerOpenCheckout() {
      EventBus.$emit(openCheckoutDialog)
    },
    triggerOpenLogin() {
      EventBus.$emit(openLoginDialog)
    },
    triggerOpenRegister() {
      EventBus.$emit(openRegisterDialog)
    },
    checkAdmin() {
      this.admin = User.isAdmin
    },
    goHome() {
      this.$router.push({name: 'home'})
    },
    goAdmin() {
      this.$router.push({name: 'admin'})
    },
    logout() {
      setTimeout(() => {
        User.isAdmin = false
        this.admin = false
        this.$router.push({name: 'home'})
      }, 500)
    }
  },
  mounted() {
    
  }
}

</script>


<style scoped>

.always-somewhere {
  position: fixed;
  z-index: 3;
}

.offset-button {
  display: flex;
  align-self: flex-end;
}

</style>
