<template>
  <v-dialog
    v-model="dialog"
    max-width="800"
  >
    <v-card>
      <v-card-text>
        <v-container grid-list-md>
          <v-layout wrap>
            <v-flex xs12>
              <v-text-field
                color="indigo"
                v-model="company.name"
                label="Name"
                hint="We record everything."
                :value="company ? company.name : ''"
              ></v-text-field>
            </v-flex>
            <v-flex xs12>
              <v-text-field
                color="indigo"
                v-model="company.image"
                label="Image URL"
                hint="We record everything."
                :value="company ? company.image : ''"
              ></v-text-field>
            </v-flex>
            <v-flex xs12>
              <v-textarea
                color="indigo"
                v-model="company.description"
                label="Description"
                hint="We record everything."
                :value="company ? company.description : ''"
              ></v-textarea>
            </v-flex>
            <v-flex xs12>
              <v-text-field
                color="indigo"
                v-model="company.ceo"
                label="CEO"
                hint="We record everything."
                :value="company ? company.ceo : ''"
              ></v-text-field>
            </v-flex>
            <v-flex xs12>
              <v-text-field
                color="indigo"
                v-model="company.market_value"
                label="Market Value"
                hint="We record everything."
                :value="company ? company.market_value : ''"
                mask="############"
              ></v-text-field>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="indigo darken-1" flat @click="dialog = false">Close</v-btn>
        <v-btn color="indigo darken-1" flat @click="isAdding ? triggerCompanyAdd() : triggerCompanyEdit()">Save</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>

import { EventBus, openEditDialog, companyWasEdited, companyWasAdded } from '@/scripts/event-bus'
import { User } from '@/scripts/user'

export default {
  data() {
    return {
      dialog: false,
      company: {},
      isAdding: false
    }
  },
  methods: {
    setCompany() {
      this.company = User.company
    },
    triggerCompanyEdit() {
      // console.log(JSON.stringify(this.company))
      fetch('http://lest-cst336.herokuapp.com/final/api/products.php', {
        method: 'PATCH',
        body: JSON.stringify(this.company)
      })
      .then(response1 => {
        return response1.text()
      })
      .then(response2 => {
        console.log(response2)
        EventBus.$emit(companyWasEdited)
        this.dialog = false
      })
    },
    triggerCompanyAdd() {
      this.company.country = ''
      this.company.headquarters = ''
      this.company.category = 1

      fetch('http://lest-cst336.herokuapp.com/final/api/products.php', {
        method: 'POST',
        body: JSON.stringify(this.company)
      })
      .then(response1 => {
        return response1.text()
      })
      .then(response2 => {
        console.log(response2)
        EventBus.$emit(companyWasAdded)
        this.dialog = false
      })
    }
  },
  mounted() {
    EventBus.$on(openEditDialog, () => {
      this.dialog = true
      this.isAdding = User.company != undefined
      this.setCompany()
    })
  }
}

</script>

