<template>
  <v-dialog v-model="dialog" fullscreen>
    <v-card class="indigo lighten-2">
      <v-toolbar dark fixed color="indigo">
        <v-btn icon dark @click="dialog = false">
          <v-icon>close</v-icon>
        </v-btn>
        <v-toolbar-title>Cart</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-toolbar-items>
          <v-btn dark flat @click="purchase()">Checkout = ${{total}} million</v-btn>
        </v-toolbar-items>
      </v-toolbar>
      <v-layout align-center column class="super-off-the-top indigo lighten-4">
        <div v-for="item in cart" :key="item.name" style="padding-bottom: 2em">
          <Company :company="item"/>
        </div>
      </v-layout>
    </v-card>
  </v-dialog>
</template>

<script>

import Company from '@/components/Company'
import { EventBus, openCheckoutDialog } from '@/scripts/event-bus'
import { User } from '@/scripts/user'

export default {
  components: {
    Company
  },
  data() {
    return {
      dialog: false,
      cart: [],
      total: 0
    }
  },
  methods: {
    updateCart() {
      this.cart = User.cart
      this.$forceUpdate()
    },
    purchase() {
      for (let item of this.cart) {
        fetch('http://lest-cst336.herokuapp.com/final/api/purchases.php', {
          method: 'post',
          body: JSON.stringify({
            productId: item.compId,
            unitPrice: item.market_value,
            purchaseDate: new Date().toISOString().substring(0, 10)
          })
        })
        .then(response1 => {
          return response1.text()
        })
        .then(response2 => {
          console.log(response2)
          this.cart = []
          this.total = 0
          User.cart = {}
        })
      }

      this.dialog = false
    }
  },
  mounted() {
    EventBus.$on(openCheckoutDialog, () => {
      this.cart = []

      for (let str of Object.keys(User.cart)) {
        this.cart.push(JSON.parse(str))
      }

      for (let item of this.cart) {
        this.total += Number(item.market_value)
      }
      
      this.dialog = true
    })
  }
}

</script>

<style scoped>

.super-off-the-top {
  padding-top: 6em;
}

</style>
