<template>
  <v-dialog
    v-model="dialog"
    max-width="600"
  >
    <v-card>
      <v-card-title>
        <span class="headline">Log Yourself In</span>
      </v-card-title>
      <v-card-text>
        <v-container grid-list-md>
          <v-layout wrap>
            <v-flex xs12>
              <v-text-field
                color="indigo"
                v-model="username"
                label="Username*"
                hint="We record everything."
                persistent-hint
                required
              ></v-text-field>
            </v-flex>
            <v-flex xs12>
              <v-text-field
                color="indigo"
                v-model="password"
                label="Password*" 
                type="password"
                hint="We record everything."
                persistent-hint
                required
              ></v-text-field>
            </v-flex>
          </v-layout>
        </v-container>
        <small>*indicates required field</small>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="indigo darken-1" flat @click="dialog = false">Close</v-btn>
        <v-btn color="indigo darken-1" flat @click="login">Login</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>

import { EventBus, openLoginDialog } from '@/scripts/event-bus'
import { User } from '@/scripts/user'

export default {
  data() {
    return {
      dialog: false,
      username: '',
      password: ''
    }
  },
  methods: {
    login() {
      // let formData = new FormData()
      // formData.append('username', this.username)
      // formData.append('password', this.password)

      fetch('http://lest-cst336.herokuapp.com/final/api/session.php', {
        method: 'POST',
        body: JSON.stringify({username: this.username, password: this.password})
      })
      .then((response1) => {
        return response1.text()
      })
      .then((response2) => {
        if (response2) {
          User.isAdmin = true
        }

        this.dialog = false
      })

      this.$forceUpdate()
    }
  },
  mounted() {
    EventBus.$on(openLoginDialog, () => this.dialog = true)
  }
}

</script>
