<template>
  <v-dialog
    v-model="dialog"
    max-width="1000"
  >
    <v-card>
      <v-card-title>
        <span class="headline">Delete this company?</span>
      </v-card-title>
      <Company :company="company" :isShopping="false"/>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="indigo darken-1" flat @click="dialog = false">Cancel</v-btn>
        <v-btn color="indigo darken-1" flat @click="triggerCompanyDelete()">Yes</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>

import Company from '@/components/Company'
import { User } from '@/scripts/user'
import { EventBus, openDeleteDialog, companyWasDeleted } from '@/scripts/event-bus'

export default {
  components: {
    Company
  },
  data() {
    return {
      dialog: false,
      company: {}
    }
  },
  methods: {
    triggerCompanyDelete() {
      let formData = new FormData()
      formData.append('id', this.company.compId)

      fetch('http://lest-cst336.herokuapp.com/final/api/products.php', {
        method: 'DELETE',
        body: '{"id": ' + this.company.compId.toString() + '}'
      })
      .then((response1) => {
        return response1.text()
      })
      .then((response2) => {
        console.log(response2)
        EventBus.$emit(companyWasDeleted)
        this.dialog = false
      })
    }
  },
  mounted() {
    EventBus.$on(openDeleteDialog, () => {
      this.dialog = true
      this.company = User.company
    })
  }
}

</script>

